  &__star-2:nth-of-type(60) {
    top: 17vh;
    left: 17vw;
    width: 4px;
    height: 4px;
    -webkit-animation-delay: 3s;
    animation-delay: 3s;
  }
  &__star-2:nth-of-type(60):before {
    width: 8px;
    height: 8px;
    top: -250%;
  }
}

.moon {
  perspective: 2000px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  &__container-title {
    width: 600px;
    height: 450px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    color: white;
    line-height: 1;
    font-weight: 700;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    display: flex;
  }
  &__title > * {
    display: inline-block;
    font-size: 200px;
  }
  &__number {
    text-shadow: 20px 20px 20px rgba(0, 0, 0, 0.2);
    padding: 0 0.2em;
    font-family: "Russo One", sans-serif;
  }
  &__subtitle {
    font-size: 25px;
    margin-top: 1.5em;
    font-family: "Lato", sans-serif;
    text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2);
  }
  &__button {
    font-size: 22px;
    margin-top: 1.5em;
    padding: 0.5em 1em;
    letter-spacing: 1px;
    font-family: "Lato", sans-serif;
    color: white;
    background-color: transparent;
    border: 0;
    cursor: pointer;
    z-index: 999;
    border: 2px solid white;
    border-radius: 5px;
    text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2);
    transition: opacity 0.2s ease;
  }
  &__button:hover {
    opacity: 0.7;
  }
  &__button:focus {
    outline: 0;
  }
  &__moon {
    position: relative;
    border-radius: 50%;
    width: 160px;
    height: 160px;
    z-index: 2;
    background-color: #fff;
    box-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #fff,
      0 0 70px #fff, 0 0 80px #fff, 0 0 100px #ff1177;
    -webkit-animation: rotate 5s ease-in-out infinite;
    animation: rotate 5s ease-in-out infinite;
  }
  &__face {
    top: 60%;
    left: 47%;
    position: absolute;
  }
  &__mouth {
    border-top-left-radius: 50%;
    border-bottom-right-radius: 50%;
    border-top-right-radius: 50%;
    background-color: #5c3191;
    width: 25px;
    height: 25px;
    position: absolute;
    -webkit-animation: snore 5s ease-in-out infinite;
    animation: snore 5s ease-in-out infinite;
    transform: rotate(45deg);
    box-shadow: inset -4px -4px 4px rgba(0, 0, 0, 0.3);
  }
  &__eyes {
    position: absolute;
    top: -30px;
    left: -30px;
  }
  &__eye-left,
  &__eye-right {
    border: 4px solid #5c3191;
    width: 30px;
    height: 15px;
    border-bottom-left-radius: 100px;
    border-bottom-right-radius: 100px;
    border-top: 0;
    position: absolute;
  }
  &__eye-left:before,
  &__eye-left:after,
  &__eye-right:before,
  &__eye-right:after {
    content: "";
    position: absolute;
    border-radius: 50%;
    width: 4px;
    height: 4px;
    background-color: #5c3191;
    top: -2px;
    left: -4px;
  }
  &__eye-left:after,
  &__eye-right:after {
    left: auto;
    right: -4px;
  }
  &__eye-right {
    left: 50px;
  }
  &__credential {
    margin-top: 30px;
    text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2);
    a {
      text-decoration: underline;
    }
  }
}

@-webkit-keyframes rotate {
  0%,
  100% {
    transform: rotate(-8deg);
  }
  50% {
    transform: rotate(0deg);
  }
}

@keyframes rotate {
  0%,
  100% {
    transform: rotate(-8deg);
  }
  50% {
    transform: rotate(0deg);
  }
}
@-webkit-keyframes snore {
  0%,
  100% {
    transform: scale(1) rotate(30deg);
  }
  50% {
    transform: scale(0.5) rotate(30deg);
    border-bottom-left-radius: 50%;
  }
}
@keyframes snore {
  0%,
  100% {
    transform: scale(1) rotate(30deg);
  }
  50% {
    transform: scale(0.5) rotate(30deg);
    border-bottom-left-radius: 50%;
  }
}
@-webkit-keyframes twinkle {
  0%,
  100% {
    opacity: 0.7;
  }
  50% {
    opacity: 0.3;
  }
}
@keyframes twinkle {
  0%,
  100% {
    opacity: 0.7;
  }
  50% {
    opacity: 0.3;
  }
}

@media screen and (max-width: 580px) {
  .moon {
    &__container-title {
      width: 100%;
    }
    &__number {
      font-size: 100px;
    }
    &__subtitle {
      font-size: 15px;
      padding: 0 1em;
    }
    &__moon {
      width: 100px;
      height: 100px;
    }
    &__face {
      transform: scale(0.7);
    }
    &__credential {
      font-size: 12px;
    }
  }
}
